class LoginController {

	constructor($cordovaOauth, $rootScope, Instagram) {
		let vm = this;
		vm.$cordovaOauth = $cordovaOauth;
		vm.$rootScope = $rootScope;
		vm.Instagram = Instagram;

	}

	login() {
		let vm = this;
		//vm.$cordovaOauth.instagram("8bbd5711d11c4bf9868c490b154c0c3f").then(function(data) {
		hello('instagram').login().then(function(data) {
			window.localStorage.token = data.authResponse.access_token;
			vm.Instagram.getUserSelf().then(function(data) {
				vm.$rootScope.currentUser = data;
				navigation.changePage('app/timeline/timeline.html');
			});
			
		}, function(e) {
			alert('Signin error: ' + e);
		});
	}
}

export default LoginController;